import { mapGetters } from 'vuex';
export default {
    data () {
		return {
			arr:[],
		};
    },
	props: ['msgDatas'],
	computed: {
		i18n () {
		  return this.$t('listModules')  
		},
	},
	watch: {
		USERACTIVE: {
			handler (a, b) {
				this.getClickRecord();
			},
			deep: true
		}
	},
	filters:{
		moneyInt(val) {
			return val.split('.')[0]
		}
	},
	created() {
		this.arr.push(this.msgDatas)
	},
	mounted() {
		
	},
    methods: {
		gotocapital(e){
			console.log('to陌拜资本详情');
			uni.navigateTo({
				url:'/modules/find/findlist/find-capital?id='+e
			})
		},
    }
};
